<template>
  <div class="container">
    <div class="inner">
      <p> *    . </p>
      <div class="inner-form">
        <el-form 
          ref="form" 
          :model="form"
          label-position="top"
          label-width="100px">
          <el-form-item 
            label=""
            placeholder=" 15  .">
            <el-input 
              v-model="form.title" />
          </el-form-item> 
          <el-form-item 
            label=" ">
            <el-select
              v-model="form.menu" 
              placeholder=" ?">
              <el-option
                v-for="food in foods"
                :key="food.food_id"
                :label="food.food_name"
                :value="food.food_id" />
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select 
              v-model="form.ratings"
              width="30px"
              placeholder="/?" >
              <el-option 
                label=":  " 
                value="5" />         
              <el-option 
                label=":  " 
                value="4" />
              <el-option 
                label=":  " 
                value="3" />
              <el-option 
                label=":   " 
                value="2" />
              <el-option 
                label=": " 
                value="1" />
            </el-select>
          </el-form-item>
          <el-form-item 
            label=" ">
            <el-input 
              v-model="form.review"
              placeholder="  . :)" 
              type="textarea"/>
          </el-form-item>
          <el-form-item>
            <UserImagePreview @child="setData"/>
          </el-form-item>
          <el-form-item label=" ">
            <el-radio-group v-model="form.status">
              <el-radio 
                label="true">
                
              </el-radio>
              <el-radio 
                label="false">
                
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button  
              type="default"
              class="btn-review"
              @click="sendReview()">
            </el-button>
            <el-button @click="returnBoard()"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import UserImagePreview from './UserImagePreview.vue';

export default {
  components: {
    UserImagePreview,
  },
  data() {
    return {
      files: [],
      form: {
        title: '',
        menu:'',
        review: '',
        status: '',
      }
    }
  },
  created() {
    this.$store.commit('food/getState')
  },
  computed: {
    ...mapState('food', ['foods']),
    
    setDataWatch() {
      return console.log(this.files)
    }
  },
  methods: {
    setData(e) {
      console.log(e);
      this.files = e
    },
    thisFilesCheck() {
      console.log("this.files", this.files)
    },
    returnBoard() {
      this.$router.push('/user/board');
    },
    sendReview() {
      let formData = new FormData(); 
      for(let i = 0; i < this.files.length; i++) {
        let file = this.files[i].file;
        formData.append("file", file);
      }
      formData.append("title", this.form.title);
      formData.append("menu", this.form.menu); 
      formData.append("ratings", this.form.ratings); 
      formData.append("review", this.form.review);
      formData.append("status", this.form.status);
      
      axios.post(`${process.env.VUE_APP_URL}/api/user/post/comment`, 
      formData, { 
        headers: {
          'Content-Type': 'multipart/form-data'
        },
      })      
      .then((res) => {
        console.log("  ", res);
        alert('  .')
        this.$router.push('/user/board')
      })
      .catch((err) => {
        console.error(" ", err);
      });
    },
  }
};

</script>

<style lang="scss" scoped>
.container {
  padding: 0px 15px;
}

.btn-review {
  // margin-top: 10px;
  position: absolute;
  top: 0px;
  right: 10px;
  font-size: 0.8rem;
  color: white;
  background-color: rgba($color: #000000, $alpha: 0.8);
}

.img-box{
  border: solid;
}

p {
  margin: 10px 0px;
}
</style>